import loadContainer from './components/container';
import loadRowCol from './components/row-col';
import loadDivBasic from './components/basic-div';

export default (editor, opts = {}) => {
    opts['bastionModel'] = {
        defaults: {
            tagName: 'div',
            attributes: {
                class: "min-h-default ",
            },
        },
    }

    loadContainer(editor, opts);
    loadRowCol(editor, opts);
    loadDivBasic(editor, opts);

    editor.on('load', function(){
        setTimeout(
            () => {
                editor.CssComposer.addRules(`.min-h-default {min-height: 50px;} .min-w-default {min-width: 100px;}`);
            },
            1000
        )
    })
};
