import loadComponents from './components';
import loadBlocks from './blocks';
import en from './locale/en';
import vi from './locale/vi';

export default (editor, opts = {}) => {
    const prefixID = opts?.['prefixID'] || 'bootstrap-layout';
    const prefixLabel = opts?.['prefixLabel'] || '';

    const options = {
        ...{
            i18n: {},
            prefixID: prefixID,
            prefixLabel: prefixLabel,

            idContainer: `${prefixID}-container`, labelContainer: `${prefixLabel} Container`,
            idContainerFluid: `${prefixID}-container-fluid`, labelContainerFluid: `${prefixLabel} Container Fluid`,

            idRow: `${prefixID}-row`, labelRow: `${prefixLabel} Row`,
            idCol: `${prefixID}-col`, labelCol: `${prefixLabel} Column`,

            idDivBasic: `div-basic`, 
            labelDiVBasic: `Div Basic`,

            categoryConfig: {
                // it when append to addBlock first with defined new category 
                //    --> after first use label for category.
                label: 'Layout',
                order: 0,
                open: true,
                ...opts?.['category'] || {},
            },
        }, ...opts
    };

    // Load i18n files
    editor.I18n && editor.I18n.addMessages({
        en, vi,
        ...options.i18n,
    });

    // Add components
    loadComponents(editor, options);
    // Add blocks
    loadBlocks(editor, options);
};