import { dataConst } from "./const";

export default (editor, opts = {}) => {
    const {categoryConfig} = opts;

    const {idDivBasic, labelDiVBasic} = opts;
    editor.BlockManager.add(idDivBasic, {
        label: labelDiVBasic,
        content: {type: idDivBasic},
        media: dataConst.iconDivBasic,
        category: categoryConfig,
    })

    const { idContainer, labelContainer } = opts;
    editor.BlockManager.add(idContainer, {
        label: labelContainer,
        content: { type: idContainer },
        media: dataConst.iconContainer,
        category: categoryConfig,
    });

    const { idContainerFluid, labelContainerFluid } = opts;
    editor.BlockManager.add(idContainerFluid, {
        label: labelContainerFluid,
        content: { type: idContainerFluid },
        media: dataConst.iconContainerFluid,
        category: categoryConfig,
    });

    const { idRow, labelRow } = opts;
    editor.BlockManager.add(idRow, {
        label: labelRow,
        content: { type: idRow },
        media: dataConst.iconRow,
        category: categoryConfig,
    })

    const { idCol, labelCol } = opts;

    editor.BlockManager.add(`${idCol}-6-6`, {
        label: `2 ${labelCol} (6-6)`,
        content: [
            { type: idCol, colNum: 6 },
            { type: idCol, colNum: 6 },
        ],
        media: dataConst.icon66,
        category: categoryConfig,
    });

    editor.BlockManager.add(`${idCol}-4-4-4`, {
        label: `3 ${labelCol} (4-4-4)`,
        content: [
            { type: idCol, colNum: 4 },
            { type: idCol, colNum: 4 },
            { type: idCol, colNum: 4 },
        ],
        media: dataConst.iconCol444,
        category: categoryConfig,
    });

    
    editor.BlockManager.add(`${idCol}-3-3-3-3`, {
        label: `4 ${labelCol} (3-3-3-3)`,
        content: [
            { type: idCol, colNum: 3 },
            { type: idCol, colNum: 3 },
            { type: idCol, colNum: 3 },
            { type: idCol, colNum: 3 },
        ],
        media: dataConst.iconCol3333,
        category: categoryConfig,
    });

    
    editor.BlockManager.add(`${idCol}-4-8`, {
        label: `2 ${labelCol} (4-8)`,
        content: [
            { type: idCol, colNum: 4 },
            { type: idCol, colNum: 8 },
        ],
        media: dataConst.iconCol48,
        category: categoryConfig,
    });

    editor.BlockManager.add(`${idCol}-12`, {
        label: `${labelCol}`,
        content: { type: idCol, colNum: 12 },
        media: dataConst.iconColX,
        category: categoryConfig,
    });
}
