export default (editor, opts) => {
    const { bastionModel, idContainer, idContainerFluid } = opts;

    const { idRow, labelRow } = opts;
    editor.DomComponents.addType(idRow, {
        model: {
            ...bastionModel,
            defaults: {
                ...bastionModel.defaults,
                name: labelRow,
                tagName: 'div',
                attributes: {
                    class: bastionModel.defaults.attributes.class + 'row p-0',
                },
                droppable: true,
                draggable: (target, destination) => {
                    return destination.get('type') === idContainer || destination.get('type') === idContainerFluid || destination.get('type') === idCol;
                },
            }
        },
        isComponent: el => {
            return el.tagName === 'DIV' && el.classList.contains('row');
        },
    });

    const { idCol, labelCol } = opts;
    editor.DomComponents.addType(idCol, {
        model: {
            ...bastionModel,
            defaults: {
                ...bastionModel.defaults,
                name: labelCol,
                tagName: 'div',
                attributes: {
                    class: bastionModel.defaults.attributes.class + ' col p-0',
                },
                droppable: true,
                draggable: (target, destination) => {
                    return destination.get('type') === idRow;
                },

                colNum: '12',
                colSMNum: '0',
                colMDNum: '0',
                colLGNum: '0',
                colXLNum: '0',
                traits: [
                    {
                        name: 'colNum',
                        label: 'Column Number (default)',
                        type: 'select',
                        options: [
                            {id: '', name: '0'},
                            {id: '1', name: '1'},
                            {id: '2', name: '2'},
                            {id: '3', name: '3'},
                            {id: '4', name: '4'},
                            {id: '5', name: '5'},
                            {id: '6', name: '6'},
                            {id: '7', name: '7'},
                            {id: '8', name: '8'},
                            {id: '9', name: '9'},
                            {id: '10', name: '10'},
                            {id: '11', name: '11'},
                            {id: '12', name: '12'},
                        ],
                        changeProp: 1,
                    },
                    {
                        name: 'colSMNum',
                        label: 'Small',
                        type: 'select',
                        options: [
                            {id: '', name: '0'},
                            {id: '1', name: '1'},
                            {id: '2', name: '2'},
                            {id: '3', name: '3'},
                            {id: '4', name: '4'},
                            {id: '5', name: '5'},
                            {id: '6', name: '6'},
                            {id: '7', name: '7'},
                            {id: '8', name: '8'},
                            {id: '9', name: '9'},
                            {id: '10', name: '10'},
                            {id: '11', name: '11'},
                            {id: '12', name: '12'},
                        ],
                        changeProp: 1,
                    },
                    {
                        name: 'colMDNum',
                        label: 'Medium',
                        type: 'select',
                        options: [
                            {id: '', name: '0'},
                            {id: '1', name: '1'},
                            {id: '2', name: '2'},
                            {id: '3', name: '3'},
                            {id: '4', name: '4'},
                            {id: '5', name: '5'},
                            {id: '6', name: '6'},
                            {id: '7', name: '7'},
                            {id: '8', name: '8'},
                            {id: '9', name: '9'},
                            {id: '10', name: '10'},
                            {id: '11', name: '11'},
                            {id: '12', name: '12'},
                        ],
                        changeProp: 1,
                    },
                    {
                        name: 'colLGNum',
                        label: 'Large',
                        type: 'select',
                        options: [
                            {id: '', name: '0'},
                            {id: '1', name: '1'},
                            {id: '2', name: '2'},
                            {id: '3', name: '3'},
                            {id: '4', name: '4'},
                            {id: '5', name: '5'},
                            {id: '6', name: '6'},
                            {id: '7', name: '7'},
                            {id: '8', name: '8'},
                            {id: '9', name: '9'},
                            {id: '10', name: '10'},
                            {id: '11', name: '11'},
                            {id: '12', name: '12'},
                        ],
                        changeProp: 1,
                    },
                    {
                        name: 'colXLNum',
                        label: 'Extra Large',
                        type: 'select',
                        options: [
                            {id: '', name: '0'},
                            {id: '1', name: '1'},
                            {id: '2', name: '2'},
                            {id: '3', name: '3'},
                            {id: '4', name: '4'},
                            {id: '5', name: '5'},
                            {id: '6', name: '6'},
                            {id: '7', name: '7'},
                            {id: '8', name: '8'},
                            {id: '9', name: '9'},
                            {id: '10', name: '10'},
                            {id: '11', name: '11'},
                            {id: '12', name: '12'},
                        ],
                        changeProp: 1,
                    },
                ],
                "script-prop": ['colNum', 'colSMNum', 'colMDNum', 'colLGNum', 'colXLNum'],
            },
            init() {
                this.setAttributes({
                    class: `${this.getAttributes().class} col-${this.get('colNum')}`
                })

                this.on('change:colNum', this.generateCol);
            },
            generateCol(){
                let colGrid = this.get('colGrid');
                let colNum = this.get('colNum');
                
                
                console.log('generateCol');
                let cleanedCls = this.getAttributes().class.replace(/\b(col-|col-sm-|col-md-|col-lg-|col-xl-)([1-9]|1[0-2])\b/g, '') + ` col-${colGrid ? colGrid + '-' : ''}${colNum}`;

                this.setAttributes({ class: cleanedCls.replace(/\s+/g, ' ') });
            },
        },
        isComponent: el => {
            return el.tagName === 'DIV' && el.classList.contains('col');
        },
    });
}