export default (editor, opts = {}) => {
    const {idDivBasic, labelDiVBasic} = opts;

    editor.DomComponents.addType(idDivBasic, {
        model: {
            defaults: {
                name: labelDiVBasic,
                tagName: 'div',
                attributes: {
                    class: 'min-h-default min-w-default',
                }
            }
        }
    })
}