export default (editor, opts = {}) => {
    const {
        bastionModel,
        idContainer, labelContainer,
        idContainerFluid, labelContainerFluid,
    } = opts;
    
    editor.DomComponents.addType(idContainer, {
        model: {
            ...bastionModel,
            defaults: {
                ...bastionModel.defaults,
                name: labelContainer,
                tagName: 'div',
                attributes: {
                    class: bastionModel.defaults.attributes.class + 'container pb-3',
                },
            },
        },
        isComponent: el => {
            return el.tagName === 'DIV' && el.classList.contains('container');
        },
    });

    editor.DomComponents.addType(idContainerFluid, {
        model: {
            ...bastionModel,
            defaults: {
                ...bastionModel.defaults,
                name: labelContainerFluid,
                tagName: 'div',
                attributes: {
                    class: bastionModel.defaults.attributes.class + 'container-fluid pb-3',
                },
            }
        },
        isComponent: el => {
            return el.tagName === 'DIV' && el.classList.contains('container-fluid');
        },
    })
}